<template>
  <div>
    <in-body :mbx="['首页','帮助手册','ES6帮助手册']">

   <Row>
     <Col span="6">

<div>let 和 const 命令</div>

     </Col>
     <Col span="18">

       <Divider orientation="left">let 和 const 命令</Divider>
       <Row class="trClass">
         <Col :span="leftSpan">命令代码</Col><Col :span="rightSpan">命令说明</Col>
       </Row>
        <Row class="trClass2">
       <Col :span="leftSpan">...</Col><Col :span="rightSpan">展开数组</Col>
     </Row>
       <Row class="trClass2">
         <Col :span="leftSpan">var f = v => v;</Col><Col :span="rightSpan">
         // 等同于
         var f = function (v) {
         return v;
         };
       </Col>
       </Row>


     </Col>
   </Row>

    </in-body>
  </div>
</template>

<script>
  import inBody from '../inBody'
    export default {
        name: "es6",
      data:function(){
          return {
              leftSpan:6
            ,rightSpan:18
          }
      },
      components:{
          inBody
      }
    }
</script>

<style scoped>
.trClass { background-color: #4d5669;
  color: #ffffff; padding:5px;}
  .trClass2 { padding:5px; background-color: #f2f2f2;}
</style>



